import { APP_INITIALIZER, Injector, LOCALE_ID, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AbpModule } from '@abp/abp.module';
import { AbpHttpInterceptor } from '@abp/abpHttpInterceptor';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { ServiceProxyModule } from '@shared/service-proxies/service-proxy.module';
import { SharedModule } from '@shared/shared.module';
import { RootRoutingModule } from './root-routing.module';

import { AppConsts } from '@shared/AppConsts';
import { API_BASE_URL } from '@shared/service-proxies/service-proxies';
import { AppSessionService } from '@shared/session/app-session.service';

import { HttpClientModule } from '@angular/common/http';
import { ModalModule } from 'ngx-bootstrap';
import { AppPreBootstrap } from './AppPreBootstrap';
import { RootComponent } from './root.component';

export function appInitializerFactory(injector: Injector) {
  return () => {
    console.log('>>> root.module.ts');
    // LISTENER DE POSTMESSAGE PARA IFRAME EXTERNO
    window.addEventListener(
      'message',
      function receiveMessage(event) {
        // if (event.origin !== "http://example.org:8080")
        if (event.data !== 'undefined') {
          const accessToken = event.data.accessToken;
          const encryptedAccessToken = event.data.encryptedAccessToken;
          const expireInSeconds = event.data.expireInSeconds;
          if (accessToken) {
            const tokenExpireDate = new Date(new Date().getTime() + 1000 * Number(expireInSeconds));
            abp.auth.setToken(accessToken, tokenExpireDate);
            abp.utils.setCookieValue(AppConsts.authorization.encrptedAuthTokenName, encryptedAccessToken, tokenExpireDate, abp.appPath);
            sessionStorage.setItem('showMenu', 'false');
          }
        }
        return;
      },
      false
    );

    abp.ui.setBusy();

    return new Promise<boolean>((resolve, reject) => {
      AppPreBootstrap.run(() => {
        abp.event.trigger('abp.dynamicScriptsInitialized');
        const appSessionService: AppSessionService = injector.get(AppSessionService);
        appSessionService.init().then(
          result => {
            abp.ui.clearBusy();
            resolve(result);
          },
          err => {
            abp.ui.clearBusy();
            reject(err);
          }
        );
      });
    });
  };
}

export function getRemoteServiceBaseUrl(): string {
  return AppConsts.remoteServiceBaseUrl;
}

export function getCurrentLanguage(): string {
  return abp.localization.currentLanguage.name;
}

@NgModule({
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedModule.forRoot(),
    ModalModule.forRoot(),
    AbpModule,
    ServiceProxyModule,
    RootRoutingModule,
    HttpClientModule
  ],
  declarations: [RootComponent],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AbpHttpInterceptor, multi: true },
    { provide: API_BASE_URL, useFactory: getRemoteServiceBaseUrl },
    {
      provide: APP_INITIALIZER,
      useFactory: appInitializerFactory,
      deps: [Injector],
      multi: true
    },
    {
      provide: LOCALE_ID,
      useFactory: getCurrentLanguage
    }
  ],
  bootstrap: [RootComponent]
})
export class RootModule {}
