import { Component, Inject, OnInit, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AppComponentBase } from '@shared/app-component-base';
import * as _ from 'lodash';
import { FormGroup, FormBuilder } from '@angular/forms';

export interface DialogData {
    type: string; // tipo: 'success' | 'warning' | 'confirm';
    title: string;
    msg: string;
}

@Component({
    selector: 'app-custom-dialog',
    templateUrl: './custom-dialog.component.html',
    styleUrls: ['./custom-dialog.component.scss']
})
export class CustomDialogComponent extends AppComponentBase implements OnInit {
    languages: abp.localization.ILanguageInfo[];
    currentLanguage: abp.localization.ILanguageInfo;
    form: FormGroup;
    constructor(private fb: FormBuilder, public dialogRef: MatDialogRef<CustomDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: DialogData,
        injector: Injector) {
            super(injector);
        }
    ngOnInit(): void {
        this.form = this.fb.group({data: true});
        this.languages = _.filter(this.localization.languages, l => !l.isDisabled);
        this.currentLanguage = this.localization.currentLanguage;
    }
    save() {
        this.dialogRef.close(true);
    }

    close() {
        this.dialogRef.close(false);
    }
}
