﻿import { CompilerOptions, NgModuleRef, Type } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppConsts } from '@shared/AppConsts';
import * as moment from 'moment-timezone';

export class AppPreBootstrap {
  static run(callback: () => void): void {
    AppPreBootstrap.getApplicationConfig(() => {
      AppPreBootstrap.getUserConfiguration(callback);
    });
  }

  static bootstrap<TM>(moduleType: Type<TM>, compilerOptions?: CompilerOptions | CompilerOptions[]): Promise<NgModuleRef<TM>> {
    return platformBrowserDynamic().bootstrapModule(moduleType, compilerOptions);
  }

  private static getApplicationConfig(callback: () => void) {
    return abp
      .ajax({
        url: '/assets/appconfig.json',
        method: 'GET',
        headers: {
          'Abp.TenantId': abp.multiTenancy.getTenantIdCookie()
        }
      })
      .done((result: any) => {
        let env = result.environment;
        if (env === 'Development') {
          env = '';
        } else {
          env = '.' + env;
        }
        const configURl = '/assets/appconfig' + env + '.json';
        abp
          .ajax({
            url: configURl,
            method: 'GET',
            headers: {
              'Abp.TenantId': abp.multiTenancy.getTenantIdCookie()
            }
          })
          .done((cfgResult: any) => {
            AppConsts.appBaseUrl = cfgResult.appBaseUrl;
            AppConsts.remoteServiceBaseUrl = cfgResult.remoteServiceBaseUrl;
            callback();
          });
      });
  }

  private static getCurrentClockProvider(currentProviderName: string): abp.timing.IClockProvider {
    if (currentProviderName === 'unspecifiedClockProvider') {
      return abp.timing.unspecifiedClockProvider;
    }

    if (currentProviderName === 'utcClockProvider') {
      return abp.timing.utcClockProvider;
    }

    return abp.timing.localClockProvider;
  }

  private static getUserConfiguration(callback: () => void): JQueryPromise<any> {
    return abp
      .ajax({
        url: AppConsts.remoteServiceBaseUrl + '/AbpUserConfiguration/GetAll',
        method: 'GET',
        headers: {
          'Authorization': 'Bearer ' + abp.auth.getToken(),
          '.AspNetCore.Culture': abp.utils.getCookieValue('Abp.Localization.CultureName'),
          'Abp.TenantId': abp.multiTenancy.getTenantIdCookie()
        }
      })
      .done((result: any) => {
        $.extend(true, abp, result);

        abp.clock.provider = this.getCurrentClockProvider(result.clock.provider);
        moment.locale(abp.localization.currentLanguage.name);

        if (abp.clock.provider.supportsMultipleTimezone) {
          moment.tz.setDefault(abp.timing.timeZoneInfo.iana.timeZoneId);
        }

        callback();
      });
  }
}
