﻿import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AppAuthService } from '@shared/auth/app-auth.service';
import { AppRouteGuard } from '@shared/auth/auth-route-guard';
import { AppUrlService } from '@shared/nav/app-url.service';
import { LocalizePipe } from '@shared/pipes/localize.pipe';
import { AppSessionService } from '@shared/session/app-session.service';
import { AbpModule } from 'abp-ng2-module/dist/src/abp.module';
import { BlockDirective } from './directives/block.directive';
import { BusyDirective } from './directives/busy.directive';
import { EqualValidator } from './directives/equal-validator.directive';
import { AbpPaginationControlsComponent } from './pagination/abp-pagination-controls.component';

import { MaterialInput } from '@shared/directives/material-input.directive';
/*componentes Plus*/
import { MaterialModule } from '@shared/material.module';
import { CustomDialogComponent } from '@shared/plus-dialog/custom-dialog.component';
import { PlusNotificatorComponent } from '@shared/plus-notificator/plus-notificator.component';

@NgModule({
  imports: [CommonModule, AbpModule, RouterModule, MaterialModule],
  declarations: [
    AbpPaginationControlsComponent,
    LocalizePipe,
    BlockDirective,
    BusyDirective,
    EqualValidator,
    MaterialInput,
    CustomDialogComponent,
    PlusNotificatorComponent
  ],
  exports: [
    AbpPaginationControlsComponent,
    LocalizePipe,
    BlockDirective,
    BusyDirective,
    EqualValidator,
    MaterialInput,
    CustomDialogComponent,
    PlusNotificatorComponent
  ],
  entryComponents: [CustomDialogComponent]
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule,
      providers: [AppSessionService, AppUrlService, AppAuthService, AppRouteGuard]
    };
  }
}
